import React from 'react'
import { useDispatch } from 'react-redux'
import { increment } from '../../redux/CouterSlice'
import { WATER } from '../../../Assets/Image/imageWater/Water'
import '../BurgerStyle/Angus.css'


const {BONAQUA}=WATER


const BonAqua= () => {

    const dispatch = useDispatch()



  return (
    <div className='angus_container' >
        <div className='angus_page'>
            <div className='angus' >
                <img className='angus_image' src={BONAQUA} />
                <div className='ves'>
                    <p>: 500</p>
                
                </div>
                <div className='over' >
                    <p className='sostav_p' > 100  :</p>
                
                <div className='sostav'>
                    <div className='bgu'>
                        <p className='sostav_name' ></p>
                        <p className='sostav_gram' >0</p>
                    </div>
                    <div className='bgu'>
                        <p className='sostav_name' ></p>
                        <p className='sostav_gram' >0 </p>
                    </div>
                    <div className='bgu'>
                        <p className='sostav_name' ></p>
                        <p className='sostav_gram' >0 </p>
                    </div>
                    <div className='bgu'>
                        <p className='sostav_name' >.</p>
                        <p className='sostav_gram' >0 </p>
                        
                    </div>
                                   
                </div>
                </div>
            </div>
            <div className='angus_infa' >
                <div className='angus_information'>
                    <div className='rr' >
                    <h1 className='angus_name' >Bon Aqua 0.5</h1>
                    <h1 className='angus_price' >1.82 .</h1>
                    </div>
                    <div className='angus_details' >
                        <p className='angus_details_infa' ></p>
                        <p className='angus_ditails_opis' >   0,5 .</p>
                                        
                    </div>
                    <div className='button_price' >
                        
                        <button onClick={()=>{dispatch(increment({name:'Bon Aqua',image:BONAQUA,price:1.82,quantity:1}))}} > </button>
                    </div>

                </div>
            </div>
            

        </div>
    </div>
  )
}

export {BonAqua}