import React, { useState } from 'react'
import { Link, animateScroll } from 'react-scroll'
import '../Styles/Main.css'
import { Header } from './Header'
import { Footer } from './Footer'

import { AboutUs } from './AboutUs'
import { Categories } from './Categories'
import { Roll } from './Roll'
import { ChikenBurger } from './ChikenBurger'
import { Stock } from './Stock'
import { WatersCold } from './WatersCold'




const Main = () => {

  const [active,setActive]=useState(true)
 

  return (
    <div class="main">
      <div className='container_p' >  
      <div className='Stock' id='/'>
        <Stock/>
       </div>
        </div>

     <div class= 'main__items' >
      <p className='main_p' ><Link class="main__item" activeClass='active' to='/' smooth={true} duration={700} spy={true} offset={-150} ></Link></p>
      <p className='main_p' ><Link class="main__item" activeClass='active' to='Govadina' smooth={true} duration={700} spy={true} offset={-150}>  </Link></p>
      <p className='main_p' ><Link class="main__item" activeClass='active' to='Chiken_and_fish' smooth={true} duration={700} spy={true} offset={-147}>  </Link></p>
      <p className='main_p' ><Link class="main__item" activeClass='active' to='Rolls' smooth={true} duration={700} spy={true} offset={-147}></Link></p>
      <p className='main_p'> <Link class="main__item" activeClass='active' to='Water' smooth={true} duration={700} spy={true} offset={-150} ></Link></p>
      {/* <p className='main_p'> <Link class="main__item" activeClass='active' to='/' smooth={true} duration={700} spy={true} offset={-150} >Blog</Link></p>
      <p className='main_p'> <Link class="main__item" activeClass='active' to='/' smooth={true} duration={700} spy={true} offset={-150} >Sign in</Link></p> */}
    
    </div> 

    
  <div className='main_page'>
    <div  className='container_p' >
      <div id='Govadina'className='Cate' >
    <h2  >  </h2>
     <Categories/> 
    </div>
   </div>
   
   <div  className='container_p' >
    <div id='Chiken_and_fish' className='Cate' >
    <h2 >    </h2>
    <ChikenBurger/>
    </div>
   </div>
   
   <div  className='container_p' >
     <div id='Rolls' className='Cate' >
    <h2 ></h2>
    <Roll/>
    </div>
   </div> 
      
   <div  className='container_p' >
     <div id='Water' className='Cate' >
    <h2 > </h2>
    <WatersCold/>
    </div>
   </div> 
  </div>

 
  </div>
  )
}

export {Main}