import React, { useState } from 'react'
import { decrementNumber } from '../redux/CouterSlice'
import { Link} from 'react-router-dom'
import { useSelector } from 'react-redux'
import { useDispatch } from 'react-redux/es/exports'
import '../Styles/Header.css'
import { IMAGE } from '../../Assets'
import Modal from './Modal'
import MenuSmoll from './MenuSmoll'



const {BURGER,CONTACT, MENU,
  MANU ,PENCIL, EXIT,
   BUY,USERICON,}=IMAGE



const Header = () => {

const dispatch = useDispatch()
const stre = useSelector(state=>state.b.burger)
const num = useSelector(state=>state.user.user)
const [modalActive,setModal]=useState(false)
const [menuActive, setMenuActive]=useState(false)

const[profile,setProfile]=useState(false)
const [city,setCity]=useState('')

const [menu,setMenu]=useState(false)

console.log(profile)

  return (
    
    <div className='container' >
          <div className= 'item' >
            <div className='item_container' >

              
            <div className='item_ul' >
              
           
            <div className='menu_item' >
              <img className='menu' src={MENU} onClick={()=>setMenuActive(true)}/>
            </div>
            <div className='href'>
            
              <p className='Exit_buy'>  <img src={PENCIL} />
              </p>
              <span className='city'>{city}</span>
              <div className='select' >
                  <select onChange={(e)=>setCity(e.target.value)} className='select-page' >
                  <option></option>
                  <option></option>
                  <option></option>
                  <option></option>
                  <option></option>
                </select>
              </div>

            </div>
            <div className='number' >
              7766<br/>
              <span >  </span>
            </div>
           
            </div>
            <div className='image_icon'>
            <img src={BURGER} />
            </div>


            <div className='item_2' >
           
            
            <div className='href_2'>
              <Link to='/' ></Link>
            </div>
            {/* <div className='href_2' >
              <Link></Link>
            </div> */}
           <div className='href_2' >
              <Link to='About'> </Link>
            </div>
            <div className='Exit_2' onClick={()=>setModal(true)} >
            {num.length >= 1 ?
            <div/>
          :
          <p className='Exit_buy'> <img src={EXIT} className='Exit'/>
            </p>
          }
            
            </div>
            {num.length <1? 
            <div className='nonUser' ></div>
            :
            <div className='user' onClick={(e)=>setProfile(true)} >
              <img src={USERICON} onClick={(e)=>setProfile(true)} />
              
              <div className='no' >{num.map(({number})=>(
            <div className={profile ===true ? 'Profile' :'noe'}  >
                <h1></h1>
                <p  > : <span>{number}</span> </p>
                <div className='MenuCity' >
                  <p className='ProfilCity' > :<br></br> {city}</p>
                  <div className='ProfileCity' >
                  <select onChange={(e)=>setCity(e.target.value)} className='select-page' >
                  <option></option>
                  <option></option>
                  <option></option>
                  <option></option>
                  <option></option>
                </select>
              </div>
              </div>
              <div>
                <p className='ProfilExit' onClick={()=>dispatch(decrementNumber({id:2}))} ></p>
              </div>
                </div>))}
              </div>
            </div>
            }
            
            <div className='buy' >
             <Link to='k' ><img src={BUY} /> <span className='korzina_count' >{stre.length}</span> </Link> 
            </div>
           

            </div>
            </div>
      </div> 
      <div >
            <Modal active={modalActive} setActive={setModal} />
        </div>
         <div>
           <MenuSmoll active={menuActive} setActive={setMenuActive} menu={menu} setMenu={setMenu} modal={modalActive} setModal={setModal}/>
        </div>
      
    </div>
  )
}

export  {Header}