import React from 'react'
import { Water } from '../Burgers/ColdWater'
import '../Styles/Water.css'
import { CATEGORIES } from '../../Assets/Image/image/Categories'
import { increment } from '../redux/CouterSlice'
import { useDispatch,useSelector } from 'react-redux'
import { Link } from 'react-router-dom'

const {ANGUS,ANGUSXL,BIGKING,
  VOPPERD,VOPPERDABL,VOPPERCHEESE,
  DABLPVOPPERCHEESE,TILZITERKING,BEKONGAMBURGER,
  ANGUSPARMED,ANGUSPARMERDUBL,GRANDGHEESEFRESH,
  GRANDGHEESE,GAMBURGER,SWEDISHBURGER,
  CHEESEBURGER,DUBLCHEESEBURGER}=CATEGORIES



const WatersCold = () => {
  const stre = useSelector(state=>state.b.burger)
console.log(stre)
  const dispatch = useDispatch()
  
  return (
    <div className='Water_container' >
      <div className='Water' >
        
        <div className='Water_page' >
      {Water.map(({image,name, price,about,href,quantity})=>(
        <div className='Water_item' >
          <Link to={href} ><img src={image} /></Link>
          <div className='info' >
            <p className='Water_name' >{name}</p>
            <p className='Water_info' > <Link to={href} > {about}</Link></p>
            <div className='Water_buy' >
              <p className='Water_price' > {price} .</p>
              <button onClick={()=>{dispatch(increment({name,image,price,quantity}))}} ></button>
            </div>
          </div>
          </div>
      ))}
        </div>
      </div>
    </div>
  )
}

export { WatersCold}