import React from 'react'
import Slider from 'react-slick'
import { STOCK } from '../../Assets/Image/Stock/StockImage';
import '../Styles/Stock.css'
 import "slick-carousel/slick/slick.css";
 import "slick-carousel/slick/slick-theme.css";


 const { A4,NEW,NEWWATER,
    STOCKBURGER,STOCKOPICE,JOB,
    HOOT,ANGUSSTOCK,}=STOCK


const Stock = () => {

    var settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll:4
      };

  return (
    <div className='Stock_container' >
        <div className='Stock_page' >
            <Slider {...settings} >
                <div>
                <div className='img_slide'>
                <img src={A4}/>
               </div>
                </div>
                <div>
                <div className='img_slide'>  
                <img src={NEW} />
                </div></div>
                <div>
                <div className='img_slide'>
                <img src={NEWWATER} />
                </div>
                </div>
                <div>
                <div className='img_slide'>
                <img src={STOCKBURGER}/>
                </div>
                </div>
                <div>
                <div className='img_slide'>
                <img src={STOCKOPICE}/>
                </div>
                </div>
                <div>
                <div className='img_slide'>
                <img src={JOB}/>
                </div>
                </div>
                <div>
                <div className='img_slide'>
                <img src={HOOT}/>
                </div>
                </div>
                <div>
                <div className='img_slide'>
                <img src={ANGUSSTOCK}/>
                </div>
                </div>
            </Slider>
        </div>
    </div>

  )
}

export{ Stock}